@import url('https://fonts.googleapis.com/css?family=Codystar');

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  width: 100%;
  height: auto;
}

#annika {
    font-family: 'Codystar', cursive;
    color: white;
    margin-bottom: 0;
    width: 100%;
    height: 52px;
    background-color: #2e2b2b;
    margin-top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: end;
        align-items: flex-end;
    -webkit-animation: down 4s 1 ease-in-out;
            animation: down 4s 1 ease-in-out;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: auto;
  margin-left: 0;
  background-size: cover;
}

.main {
  width: 400px;
  height: 350px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 10px;
  margin-top: 40px;
}

hr {
  border: 1px solid #c93f3f;
  width: 500px;

}
.image {
  width: 200px;
  height: 300px;
  background: white;
}

img {
  width: 200px;
  height: 300px;
}

.text {
  width: 200px;
  height: 300px;
  background: white;
  -webkit-animation-name: slidein;
          animation-name: slidein;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
  padding-top: 50px;
}

p#text {
  text-align: center;
  padding-top: 10px;
  -webkit-animation-name: turnleft;
          animation-name: turnleft;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
}

h1 {
  text-align: center;
}

.info {
  width: 300px;
  height: auto;
}

/* .background:hover {
  animation: slide 4s 1 ease-in-out;
  transform : translateX(-300px);
}

.future:hover {
  animation: slideright 4s 1 ease-in-out;
  transform : translateX(300px);
} */

@-webkit-keyframes slidein {
  0% {
    -webkit-transform : translateX(-300px);
            transform : translateX(-300px);
  }

  50% {
    -webkit-transform : translateX(0px);
            transform : translateX(0px);
  }
}

@keyframes slidein {
  0% {
    -webkit-transform : translateX(-300px);
            transform : translateX(-300px);
  }

  50% {
    -webkit-transform : translateX(0px);
            transform : translateX(0px);
  }
}

@-webkit-keyframes turnleft {
  0% {
  background: #c93f3f;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

  50% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes turnleft {
  0% {
  background: #c93f3f;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

  50% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@-webkit-keyframes slide {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }

  100% {
    -webkit-transform : translateX(-300px);
            transform : translateX(-300px);
  }

}

@keyframes slide {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }

  100% {
    -webkit-transform : translateX(-300px);
            transform : translateX(-300px);
  }

}

@-webkit-keyframes slideright {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }

  50% {
    -webkit-transform : translateX(300px);
            transform : translateX(300px);
  }
}

@keyframes slideright {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }

  50% {
    -webkit-transform : translateX(300px);
            transform : translateX(300px);
  }
}

@-webkit-keyframes down {
  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes down {
  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}


@media (min-width: 601px) and (max-width: 900px) {
  html,
  .body {
    width: 100%;
    height: auto;
  }
  .text {
    padding: 0;
  }
  p#text {
    font-size: 21px;
  }
  h1 {
    font-size: 40px;
    text-align: center;
  }
}

@media (max-width: 600px){
  html,
  .body,
  .container {
    width: 140vw;
    height: auto;
  }
  .text {
    padding-top: 22px;
  }
}



