.flip-container {
  -webkit-perspective: 1000;
  -o-perspective: 1000;
  perspective: 1000;
  margin: 10px;
	border: 2px solid #f8f8f8;
}

.material-icons {
  font-size: 15vw;
  visibility: visible;
}

	.flip-container:hover .flipper,
  .flip-container.hover .flipper {
		-webkit-transform: rotateY(180deg);
		transform: rotateY(180deg);
	}

.flip-container, .front, .back {
	width: 400px;
	height: 400px;
}
.flip-container, .front, .back {
	width: 400px;
	height: 400px;
}

.flipper {
	-webkit-transition: 2s;
	-webkit-transform-style: preserve-3d;

	-moz-transition: 2s;
	-moz-transform-style: preserve-3d;

  -o-transition: 2s;
	-o-transform-style: preserve-3d;

	transition: 2s;
	transform-style: preserve-3d;

	position: relative;
}

.front, .back {
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;

	position: absolute;
	top: 0;
	left: 0;
}

.front {
	z-index: 2;
  background-size: cover;
}

.back {
	-webkit-transform: rotateY(180deg);
	transform: rotateY(180deg);

	background: #f8f8f8;
}

.front .name {
	font-size: 2em;
	display: inline-block;
	background: rgba(33, 33, 33, 0.9);
	color: #2e2b2b;
	padding: 5px 10px;
	border-radius: 5px;
	bottom: 60px;
	left: 25%;
	position: absolute;

	-webkit-transform: rotate(-20deg);
	-ms-transform: rotate(-20deg);
	    transform: rotate(-20deg);
}

.back-logo {
	position: absolute;
	top: 40px;
	left: 90px;
	width: 160px;
	height: 117px;
}

p#show {
  visibility: hidden;
  color: black;
}
.back-title {
	font-weight: bold;
	color: #2e2b2b;
	position: absolute;
	top: 10px;
	left: 0;
	right: 0;
	text-align: center;
	font-size: 2em;
}

#about-headline {
  color: #2e2b2b;
  text-align: center;
  font-size: 70px;
  font-family: 'Oxygen', sans-serif;
}
.back p#showflip {
	position: absolute;
	bottom: 40px;
	left: 0;
	right: 0;
	text-align: center;
	padding: 0 20px;
  line-height: 2em;
  font-size: 15px;
}

.about-container {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: auto;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 100px;
  padding-bottom: 100px;
  background-size: cover;
}

@media (max-width: 600px) {
  .back p {
    line-height: 1.3em;
  }
  p#show {
    visibility: visible;
    color: black;
  }
  .material-icons {
    visibility: hidden;
  }
}
